import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Divider, Row, Col , Alert } from "antd";

//const { Option } = Select;
const { TextArea } = Input;

class LocationTypeUpdateForm extends Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    //console.log(this.props.locationType);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { locationType } = this.props;
    const { loading } = locationType;
    const data = locationType.location_type ? locationType.location_type : [];
    // var phone = data.phone + "";
    // phone = phone.substring(2);
    const used = data.is_used;
    const error = locationType.errorField ? locationType.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    // const formUrl = {
    //   labelCol: {
    //     xs: { span: 24 },
    //     sm: { span: 24 }
    //   },
    //   wrapperCol: {
    //     xs: { span: 24 },
    //     sm: { span: 24 }
    //   }
    // };
    // const formCode = {
    //   labelCol: {
    //     xs: { span: 24 },
    //     sm: { span: 24 }
    //   },
    //   wrapperCol: {
    //     xs: { span: 9 },
    //     sm: { span: 9 }
    //   }
    // };
    // const formPhone = {
    //   labelCol: {
    //     xs: { span: 24 },
    //     sm: { span: 24 }
    //   },
    //   wrapperCol: {
    //     xs: { span: 18 },
    //     sm: { span: 18 }
    //   }
    // };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {used ? (
          <Row style={{ marginBottom: "20px" }}>
            <Col span={24}>
              <Alert
                message={<IntlMessages id="locationTypes.help" />}
                type="warning"
                showIcon
              />
            </Col>
          </Row>
        ) : (
          ""
        )}
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="locationTypes.add.subtitle.data" />
        </Divider> */}
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="locationTypes.add.code" />}
              validateStatus={
                error.location_type_code || getFieldError("location_type_code")
                  ? "error"
                  : null
              }
              help={
                error.location_type_code
                  ? error.location_type_code
                  : getFieldError("location_type_code")
              }
            >
              {getFieldDecorator("location_type_code", {
                initialValue: data.location_type_code,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 30,
                    message: <IntlMessages id="maxLength30" />
                  }
                ]
              })(
                <Input
                  disabled={used ? true : false}
                  //style={{ textTransform: "uppercase" }}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="locationTypes.add.name" />}
            >
              {getFieldDecorator("location_type_name", {
                initialValue: data.location_type_name,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(<Input disabled={used ? true : false} />)}
            </Form.Item>

            <Form.Item
              {...formTextArea}
              label={<IntlMessages id="locations.add.description" />}
            >
              {getFieldDecorator("description", {
                initialValue: data.description,
                // rules: [
                //   {
                //     required: true,
                //     message: <IntlMessages id="required" />
                //   }
                // ]
              })(<TextArea autosize={{ minRows: 2 }} />)}
            </Form.Item>

          </div>
        </div>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedLocationTypeUpdateForm = Form.create({ name: "update_location_type" })(LocationTypeUpdateForm);

export default WrappedLocationTypeUpdateForm;
