import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Redirect } from "react-router-dom";

import LocationTypeUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
import { Spin } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/companyMaster/locationType/actions";
import { Box } from "../../../App/app.style";

const redirectTo = '/dashboard/companies/location-types'

class UpdateLocationType extends Component {
  // Get All data diawal
  componentDidMount() {
    const { editLocationType } = this.props;
    const { id } = this.props.match.params;
    editLocationType(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.locationType.error) {
      notification("error", this.props.locationType.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.locationType.success) {
      this.props.history.replace(redirectTo);
    }
  }

  handleSubmit = value => {
    const { updateLocationType } = this.props;
    const { id } = this.props.match.params;

    // Uppercasing location code
    //value.discount = value.discount/100;
    updateLocationType(id, value);
  };

  render() {
    // console.log(this.props.locationType);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="locationTypes.update.title" />}
            style={{ margin: 0 }}
          >
            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          {this.props.locationType.loadingEdit ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
              <LocationTypeUpdateForm
                auth={this.props.auth}
                submit={this.handleSubmit}
                locationType={this.props.locationType}
              />
            )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { auth: state.auth, locationType: state.locationType };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateLocationType);
